const cacheSize = ref('23.5MB')

// Â§ÑÁêÜËÆæÁΩÆÈ°πÁÇπÂáª
const handleSetting = (type) => {
  switch (type) {
    case 'password':
      uni.navigateTo({ url: '/pages/shop/user/password' })
      break
    case 'phone':
      uni.navigateTo({ url: '/pages/shop/user/phone' })
      break
    case 'email':
      uni.navigateTo({ url: '/pages/shop/user/email' })
      break
    case 'language':
      uni.showActionSheet({
        itemList: ['ÁÆÄ‰Ωì‰∏≠Êñá', 'English', 'Êó•Êú¨Ë™û'],
        success: (res) => {
          settings.value.language = ['ÁÆÄ‰Ωì‰∏≠Êñá', 'English', 'Êó•Êú¨Ë™û'][res.tapIndex]
        }
      })
      break
    case 'clearCache':
