        title: 'ÊèêÁ§∫',
        content: 'Á°ÆÂÆöË¶ÅÊ∏ÖÈô§ÁºìÂ≠òÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            uni.showToast({
              title: 'Ê∏ÖÈô§ÊàêÂäü',
              icon: 'success'
            })
            cacheSize.value = '0B'
